import React, { useContext } from 'react'
import Navbar from '../Components/Navbar'
import Footer from '../Components/Footer'
import Header from './Header'
import { apiValue } from '../Data/AllData';
import { CartProvider, useCart } from 'react-use-cart';

function Page() {
    const data = useContext(apiValue);
    const { addItem } = useCart();
    return (
        <div className='section trending'>
            <div className="container">
                <div className='row trending-box'>
                    {data.map((item) => {
                        return (
                            <div className="col-lg-3 col-md-6 align-self-center mb-30 trending-items col-md-6 adv">
                                <div style={{ height: "320px" }} className="item">
                                    <div className="thumb">
                                        <a><img style={{ height: "200px" }} src={item.background_image} alt="" /></a>
                                        <span className="price">${item.price}</span>
                                    </div>
                                    <div className="down-content">
                                        <span className="category">{item.genres?.[0]?.name || "Unknown"} | </span> <span className="category"> {item.rating}</span>
                                        <h4 style={{ width: "210px" }}>{item.name}</h4>
                                        <a onClick={() => addItem(item)}><i className="fa-solid fa-cart-plus"></i></a>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

function AllShop() {
    return (
        <CartProvider>
            <Navbar />
            <Header />
            <Page />
            <Footer />
        </CartProvider>
    )
}

export default AllShop