import React from 'react'
import { Link } from 'react-router'

function Header() {
    return (
        <div>
            <div class="page-heading header-text">
                <div class="container">
                    <div class="row">
                        <div class="col-lg-12">
                            <h3>explore games</h3>
                            <span class="breadcrumb"><Link to="/">Home</Link> &gt; explore</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header