import React from 'react'
import Navbar from '../Components/Navbar'
import Footer from '../Components/Footer'
import Header from './Header'
import { CartProvider, useCart } from 'react-use-cart'
import { Link } from 'react-router'

function Cart() {
    const {
        isEmpty,
        cartTotal,
        totalItems,
        totalUniqueItems,
        items,
        updateItemQuantity,
        removeItem,
    } = useCart();

    if (isEmpty)
        return (
            <div className="m-auto text-center mt-5">
                <h1 className='m-4' style={{ fontSize: "50px", color: "#007cf8" }}>YOUR CART IS EMPTY</h1>
                <h1 className='m-4' style={{ fontSize: "150px", color: "#007cf8" }}><i class="fa-solid fa-store-slash"></i></h1>
                <h5 className="mb-3" style={{ fontSize: "50px", color: "#007cf8" }}>
                    <Link to="/shop"><i className="fas fa-long-arrow-alt-left me-2"></i>Go Shop</Link>
                </h5>
            </div>);

    return (
        <div className="cart">
            <section className="h-100 h-custom" style={{ backgroundColor: '#eee' }}>
                <div className="container py-5 h-100">
                    <div className="row d-flex justify-content-center align-items-center h-100">
                        <div className="col">
                            <div className="card">
                                <div className="card-body p-4">

                                    <div className="row">

                                        <div className="col-lg-7">
                                            <h5 className="mb-3"><Link to="/shop" className="text-body"><i
                                                className="fas fa-long-arrow-alt-left me-2"></i>Continue shopping</Link></h5>
                                            <hr />

                                            <div className="d-flex justify-content-between align-items-center mb-4">
                                                <div>

                                                    <p className="mb-0">You have <strong>{totalUniqueItems}</strong> items in your cart</p>
                                                </div>

                                            </div>

                                            <div className="card mb-3">
                                                <div className="card-body">
                                                    {items.map((item) => {
                                                        return (
                                                            <div className="d-flex justify-content-between">
                                                                <div className="d-flex flex-row align-items-center">
                                                                    <div>
                                                                        <img
                                                                            src={item.background_image}
                                                                            className="img-fluid rounded-3" alt={item.name} style={{ width: '65px' }}
                                                                        />
                                                                    </div>
                                                                    <div className="ms-3">
                                                                        <h5>{item.name}</h5>
                                                                        <p className="small mb-0">{item.genres?.[0]?.name || "Unknown"}</p>
                                                                    </div>
                                                                </div>

                                                                <div className="d-flex flex-row align-items-center">
                                                                    <div style={{ width: '80px' }}>
                                                                        <h5 className="mb-0">{item.quantity}</h5>
                                                                    </div>
                                                                    <div style={{ width: '80px' }}>
                                                                        <h5 className="mb-0"> ${item.price * item.quantity}</h5>
                                                                    </div>
                                                                    <a style={{ color: '#0273E3', fontSize: "25px" }} className='m-1' onClick={() => {
                                                                        updateItemQuantity(item.id, item.quantity + 1);
                                                                    }}><i class="fa-solid fa-square-plus"></i></a>
                                                                    <a style={{ color: '#838383', fontSize: "25px" }} className='m-1' onClick={() => {
                                                                        updateItemQuantity(item.id, item.quantity - 1);
                                                                    }}><i class="fa-solid fa-square-minus"></i></a>
                                                                    <a style={{ color: '#B71010', fontSize: "25px" }} className='m-1' onClick={() => {
                                                                        removeItem(item.id);
                                                                    }}><i class="fa-solid fa-square-xmark"></i></a>
                                                                </div>
                                                            </div>
                                                        )
                                                    })}

                                                </div>
                                            </div>


                                        </div>


                                        <div className="col-lg-5">

                                            <div className="card bg-primary text-white rounded-3">
                                                <div className="card-body">


                                                    <div className=" d-flex justify-content-between">
                                                        <span className="mb-2">Subtotal</span>
                                                        <span className="mb-2">${cartTotal.toFixed(2)}</span>
                                                    </div>

                                                    <div className="d-flex justify-content-between">
                                                        <span className="mb-2">Shipping</span>
                                                        <span className="mb-2">$20.00</span>
                                                    </div>

                                                    <div className="d-flex justify-content-between mb-4">
                                                        <span className="mb-2">Total (Incl. taxes)</span>
                                                        <span className="mb-2">${(cartTotal + 20).toFixed(2)}</span>
                                                    </div>

                                                    <button style={{ backgroundColor: "#ee626b", border: "none", color: "#fff" }} type="button" data-mdb-button-init data-mdb-ripple-init
                                                        className="btn btn-info btn-block btn-lg">
                                                        <div className="d-flex justify-content-between">
                                                            <span>Checkout <i className="fas fa-long-arrow-alt-right ms-2"></i></span>
                                                        </div>
                                                    </button>

                                                </div>
                                            </div>

                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

function AllCart() {
    return (
        <CartProvider>
            <Navbar />
            <Header />
            <Cart />
            <Footer />
        </CartProvider>
    )
}

export default AllCart